.top10 {
    margin-top: -9rem;
    color: white;
    margin-bottom: 3rem;
}

.top10 > h1 {
    font-size: 2.5rem;
    margin-bottom: 0rem;
    margin-left: 5rem;
}

.top10Section {
    display: flex;
    align-items: center;
    z-index: 0;
}

.top10list {
    display: flex;
    overflow: hidden;
    scrollbar-width: none;
}

.top10Btn {
    color: gray;
    font-size: 5rem;
    cursor: pointer;
    z-index: 1;
    background-color: #00ff0000;
}

.top10Btn:hover {
    color: white;
    font-size: 6rem;
    background-color: #00ff0000;
}

.listItem {
    margin-right: 5rem;
    margin-top: -2rem;
    list-style-type: none;
    display: flex;
    align-items: baseline;
    padding: 0;
}

.listItem > h2 {
    margin: -2.5rem;
    font-size: 17rem;
    height: 10rem;
    color: gray;
    font-weight: bold;
    letter-spacing: -0.5rem;
}

.listItem > img {
    height: 13rem;
}

.moveRight {
    transform: translate(-50%);
    transition-property: transform;
    transition-duration: 2s;
    transition-timing-function: linear;
}

.moveLeft {
    transform: translate(50%);
    transition-property: transform;
    transition-duration: 2s;
    transition-timing-function: linear;
}