.navigation {
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: white;
    background: linear-gradient(black, #00ff0000);
    margin: 0;
    padding: 1rem 5rem;
    font-size: 1.5rem;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
}

.logo {
    width: 7rem;
    margin: 0.2rem;
    flex-grow: 1;
}

.menu {
    text-align: left;
    flex-grow: 8;
    padding-left: 3rem;
    text-decoration: none;
}

.menuRadio {
    display: none;
}

.label {
    margin: 0 0.5rem;
    font-weight: lighter;
    font-size: 1.3rem;
    color: white;
    cursor: pointer;
}

.menuRadio:checked + .label {
    font-weight: bolder;
    font-size: 1.4rem;
}

.toolIcons {
    display: flex;
    flex-grow: 1;
    justify-content: flex-end;
    align-items: center;
}

.toolIcons div {
    margin: 0 1rem;
}

.profile {
    width: 3rem;
    display: flex;
    justify-content: space-between;
}