.randomMain {
    width: 100%;
    margin: 0;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-size: cover;
}

.coverImg {
    height: 50rem;
    width: auto;
}

.randomMainInfo {
    color: white;
    width: 35%;
}

.randomMainInfo > h1 {
    font-size: 4rem;
    line-height: 4.5rem;
    margin-bottom: 0;
}

.randomMainInfo > p {
    font-size: 1.5rem;
}

.playBtn {
    background-color: white;
    border: none;
    width: 10rem;
    height: 3.5rem;
    font-size: 1.7rem;
    font-weight: bold;
    border-radius: 0.5rem;
    align-items: center;
    cursor: pointer;
}